.popup {
  z-index: 9999;
  position: fixed;
  left: 0;
  top: 0;

  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  opacity: 0;
  visibility: hidden;
  transition: opacity .7s, visibility .7s;
}
